do la creación de prototipos de manera rápida y así obtener piezas que se ajusten a algún proyecto de tipo robótico, por supuesto. Esta tecnología es pertinente para tener el diseño ideal de un proyecto, alcanzando como resultado piezas ligeras en un menor tiempo de montaje; lo anterior se da gracias a la bondad de materiales que tienen la manufactura aditiva, dando con ello la creación o la posibilidad de obtener piezas de robótica con buenas prestaciones o rendimiento a nivel mecánico, además de producir piezas únicas y/o específicas según el proyecto; aspecto que se pude realizar con gran libertad en su diseño.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-4.png")}})])])]),_c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"titulo":"Área médica "}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("En el área médica la manufactura aditiva se ha dedicado a la implementación de la bio impresión con el fin de generar tejidos vivos, además de la fabricación de productos sanitarios que dan soporte a los médicos para algunos procedimientos.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-5.png")}})])])]),_c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"titulo":"Formación y/o educación "}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("En este sector, la tecnología aditiva permite dar vida al concepto que se expresa en el diseño CAD, materializando las ideas en objetos físicos, por consiguiente, este tipo de tecnología facilita el proceso de aprendizaje de los estudiantes desde los ciclos iniciales de la educación, como es la primaria, hasta llegar a la universidad; con este recorrido en los diferentes ciclos formativos del estudiante, se pueden imprimir de manera 3D piezas, arte o maquetas (arquitectónicas) para desarrollar prototipos.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-6.png")}})])])]),_c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"titulo":"Construcción"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Así mismo se está implementando en el sector de la construcción representado en edificios y casas; lo importante en este aspecto es que pueden salvaguardar vidas humanas en zonas de catástrofes naturales y/o guerras; esto se da debido a que los refugios para este tipo de emergencias pueden construirse con una mayor celeridad. Por dar un ejemplo, en una ciudad europea un equipo de personas utilizó la impresión 3D para darle vida a una casa de 40 metros cuadrados en un lapso de un día, otro ejemplo es en Holanda, donde pronto habrá un barrio formado por casas impresas en 3D. Además de ello este tipo de impresión permite la creación y posterior desarrollo de diseños innovadores y visiones de la arquitectura que antes no eran posibles.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-7.png")}})])])]),_c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"titulo":"Partes del cuerpo y/o de prótesis de extremidades"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Por otro lado en el sector de partes del cuerpo y/o de prótesis de extremidades esta tecnología ha tenido un impacto positivo en el sector médico, que va desde la producción de prótesis de piernas (personalizadas) para un deportista de alto rendimiento como un corredor de maratones, hasta la realización de un modelo de corazón (humano) para ayudar así a los cirujanos en este tipo de intervenciones, pasando por la fabricación de partes de animales que les falta alguna extremidad como lo fue la cola de un caimán que la había perdido cuando sólo era una cría. De manera reciente, en los Estados Unidos, más específicamente en Chicago, se realizó un experimento en el que un ratón con ovarios impresos en 3D dio a luz a crías totalmente sanas; como consecuencia de esto se cree que esto puede funcionar con nosotros los seres humanos, al seguir experimentando gracias a las investigaciones en curso.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-8.png")}})])])]),_c('div',{staticClass:"row justify-content-around align-items-center",attrs:{"titulo":"Alimentos e instrumentos musicales"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Para finalizar, las últimas aplicaciones se encuentran en los alimentos y los instrumentos musicales. En el primer caso, se imprime chocolate a partir de un diseño digital, y las empresas están comenzando a imprimir alimentos comestibles como hamburguesas, helados, masa de galletas y mazapán. En el segundo caso, ya se están fabricando flautas, bajos, violines, y otros instrumentos; por ejemplo, en una universidad en Suecia se celebró el primer concierto en directo realizado con instrumentos impresos tridimensionalmente. Además, estos instrumentos pueden utilizarse para crear accesorios como boquillas.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/T5/img-9.png")}})])])])]),_vm._m(2)],1)],1)}
